import Vue from 'vue'
import Vuex from 'vuex'



Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    URL: '',
    id: '',
      devices: Object
  },
  mutations: {
    updateState(state, devices){
      state.devices = devices;
    }
  },
  actions: {
    async updateState(ctx, devices){
      console.log('updating state...')

      let filteredDevices = {
        vacuums: devices.filter(item => item.type === 'Vacuum'),
        lights: devices.filter(item => item.type === 'Light'),
        cameras: devices.filter(item => item.type === 'Camera'),
        locks: devices.filter(item => item.type === 'Lock'),
        blinds: devices.filter(item => item.type === 'Blind'),
        acs: devices.filter(item => item.type === 'AC'),
        speakers: devices.filter(item => item.type === 'Speaker')
      }
      ctx.commit('updateState', filteredDevices);
      console.log('state updated.')
    }
  }
})
