<template>
    <section class="light" :class="{ on : state.on }" :data-state="state.state" @click="toggle">
        <h2><b>{{ state.type }}</b> {{ state.name }}</h2>
        <svg width="54" height="81" viewBox="0 0 54 81" fill="none" xmlns="http://www.w3.org/2000/svg">
                <circle cx="27" cy="27" r="27" fill="#F2F2F2" />
                <circle cx="27" cy="27" r="27" :fill="color" class="bulb" />
                <rect x="24" y="75.5" width="6" height="5" rx="2.5" fill="#D8D8D8"/>
                <rect x="18" y="74.5" width="18" height="5" rx="2.5" fill="#A6A6A6"/>
                <path d="M16 51.5H38V76.5C38 77.0523 37.5523 77.5 37 77.5H17C16.4477 77.5 16 77.0523 16 76.5V51.5Z" fill="#C4C4C4"/>
                <line x1="16" y1="55" x2="35" y2="55" stroke="black" stroke-opacity="0.1"/>
                <line x1="16" y1="63" x2="35" y2="63" stroke="black" stroke-opacity="0.1"/>
                <line x1="16" y1="71" x2="35" y2="71" stroke="black" stroke-opacity="0.1"/>
                <line x1="19" y1="59" x2="38" y2="59" stroke="black" stroke-opacity="0.1"/>
                <line x1="19" y1="67" x2="38" y2="67" stroke="black" stroke-opacity="0.1"/>
                <line x1="19" y1="75" x2="38" y2="75" stroke="black" stroke-opacity="0.1"/>
        </svg>
    </section>
</template>
<script>
export default {
    name: 'Light',
    props: {
        state: Object
    },
    methods: {
        toggle(){
            this.state.on = !this.state.on;
        }
    },
    computed: {
        color() {
            return hexToRgbA(this.state.color, this.state.brightness);
        }
    }
}

function hexToRgbA(hex, brightness){
    var c;
    if(/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)){
        c= hex.substring(1).split('');
        if(c.length== 3){
            c= [c[0], c[0], c[1], c[1], c[2], c[2]];
        }
        c= '0x'+c.join('');
        return 'rgba('+[(c>>16)&255, (c>>8)&255, c&255].join(',')+`,${brightness})`;
    }
    throw new Error('Bad Hexcode for light color.');
}

</script>
<style lang="scss">
.light {
    display: inline;

    svg {
        .bulb {
            opacity: 0;
            transition: opacity .6s ease;
        }
    }

    &.on {
        svg {
            .bulb {
                opacity: 1;
            }
        }
    }
}
</style>