<template>
  <div id="app">
    <nav>
      <router-link to="/">Devices</router-link>
      <router-link to="/about">About</router-link>
    </nav>
    <router-view/>
  </div>
</template>
<script>
export default {
  name: 'App',
  async beforeMount(){

    const es = new EventSource(`${this.URL}/stream`);

    // init
    let resp = await fetch(`${this.URL}/init`);
    let data = await resp.json();
    this.$store.dispatch('updateState', data.devices)

    // Update state on event
    es.onmessage = (state) => {
      console.log('update trigged.')

      let data = JSON.parse(state.data);
      this.$store.dispatch('updateState', data.devices)
    }
  },
  computed: {
    URL(){
      return this.$store.state.URL;
    }
  }
}
</script>
