<script>
export default {
    name: 'Lock',
    props: {
        state: Object
    },
    methods: {
        async unlock() {
            
            let code = parseInt(prompt('Enter code.'));

            if(isNaN(code)){ console.error('Code can only be numbers.') }
            
            if(code) {

                try {

                    let resp = await fetch(`${this.URL}/locks/${this.state.id}/unlock`, {
                        method: 'POST',
                        headers: {'Content-Type' : 'application/json' },
                        body: JSON.stringify({ code: code })
                    })

                    if(resp.status !== 200) {
                        console.log(resp.statusText)
                    }
                }

                catch(err) {
                    console.error(err)
                }
            }
        }
    },
    computed: {
        URL(){
            return this.$store.state.URL;
        }
    }
}
</script>
<style lang="scss">
.lock {
    display: inline;

    &.unlocked {
        svg {
            .state {
                fill: #49BF2B;
            }
        }
    }
}
</style>